import React, { useState, useEffect, useRef } from 'react';
import Country from './Components/Country';
import Box from '@mui/material/Box';
import axios from 'axios';
import AddCountry from './Components/AddCountry';
import './App.css';

const App = () => {
  const [country, setCountries] = useState([]);
  const apiEndpoint = "https://olympicmedalsbackend.azurewebsites.net/api/country";

  const AddGolds = (locaterId) => {
    setCountries((prevCountries) => {
      const updatedCountries = [...prevCountries];
      const country = updatedCountries.find((country) => country.id === locaterId);
      country.gold += 1;
      return updatedCountries;
    });
  };

  const RemoveGolds = (locaterId) => {
    setCountries((prevCountries) => {
      const updatedCountries = [...prevCountries];
      const country = updatedCountries.find((country) => country.id === locaterId);
      if (country.gold >= 1) {
        country.gold -= 1;
      } else {
        alert('You can\'t have negative medals');
      }
      return updatedCountries;
    });
  };

  const AddSilvers = (locaterId) => {
    setCountries((prevCountries) => {
      const updatedCountries = [...prevCountries];
      const country = updatedCountries.find((country) => country.id === locaterId);
      country.silver += 1;
      return updatedCountries;
    });
  };

  const RemoveSilvers = (locaterId) => {
    setCountries((prevCountries) => {
      const updatedCountries = [...prevCountries];
      const country = updatedCountries.find((country) => country.id === locaterId);
      if (country.silver >= 1) {
        country.silver -= 1;
      } else {
        alert('You can\'t have negative medals');
      }
      return updatedCountries;
    });
  };

  const AddBronzes = (locaterId) => {
    setCountries((prevCountries) => {
      const updatedCountries = [...prevCountries];
      const country = updatedCountries.find((country) => country.id === locaterId);
      country.bronze += 1;
      return updatedCountries;
    });
  };

  const RemoveBronzes = (locaterId) => {
    setCountries((prevCountries) => {
      const updatedCountries = [...prevCountries];
      const country = updatedCountries.find((country) => country.id === locaterId);
      if (country.bronze >= 1) {
        country.bronze -= 1;
      } else {
        alert('You can\'t have negative medals');
      }
      return updatedCountries;
    });
  };

  

  const handleAdd = async (name) => {
    const { data: post } = await axios.post(apiEndpoint, { name: name, gold: 0, silver: 0, bronze: 0});
    setCountries(country.concat(post));
  };

  const Delete = async(id) => {
    const originalCountry = country;
    setCountries(country.filter(c => c.id !== id));
    try {
      await axios.delete(`${apiEndpoint}/${id}`);
    } catch(ex) {
      if (ex.response && ex.response.status === 404) {
        // word already deleted
        console.log("The record does not exist - it may have already been deleted");
      } else { 
        alert('An error occurred while deleting a word');
        setCountries(originalCountry);
      }
    }
  };

  useEffect(() => {

    async function fetchData() {
      const { data: fetchedCountry } = await axios.get(apiEndpoint);
      setCountries(fetchedCountry);
    }
    fetchData();
  }, []);

  return (
    <div>
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'flex-center',
          gridTemplateColumns: 'repeat(2, 1fr)',
        }}
      >
        <AddCountry onAdd={handleAdd} />

        {country.map((country) => (
          <Country
            key={country.id}
            country={country}
            GAdd={AddGolds}
            SAdd={AddSilvers}
            BAdd={AddBronzes}
            GRemove={RemoveGolds}
            SRemove={RemoveSilvers}
            BRemove={RemoveBronzes}
            Delete={Delete}
          />
        ))}
      </Box>
    </div>
  );
};

export default App;
