import React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import AddCircleOutline from '@mui/icons-material/AddCircleOutline';
import RemoveCircleOutlineIcon from '@mui/icons-material/RemoveCircleOutline';
import Avatar from '@mui/material/Avatar';
import Grid from '@mui/material/Grid';
import Box from "@mui/material/Box";
import { amber, yellow, grey } from '@mui/material/colors';

const Country = ({ country, GAdd, SAdd, BAdd, GRemove, SRemove, BRemove, Delete }) => {
    const renderMedals = (medals) => (medals === undefined || medals === null ? 0 : medals);

    return (
        <Box
            display="flex"
            justifyContent="center"
            alignItems="center"
            minHeight="100vh"
            className='Country'
        >
            <div>
                <Container maxWidth="fit" align="center">
                    <Card sx={{ width: 400, display: "flex", justifyContent: "flex-center", margin: 2, bgcolor: grey[50] }}  >
                        <CardContent>
                            <Typography variant="h4" component="div">
                                {country.name}
                            </Typography>
                            <hr></hr>
                            <Typography variant="body2">
                                <Grid container spacing={2} align="center">
                                    <Grid item xs={4} align="center">
                                        <Grid container spacing={0}>
                                            <Grid item xs={8} align="center">
                                                <Avatar sx={{ bgcolor: yellow[500], width: 80, height: 80, fontSize: 60 }}>{renderMedals(country.gold)}</Avatar>
                                            </Grid>
                                            <Grid item xs={8} align="center">
                                                <RemoveCircleOutlineIcon onClick={() => GRemove(country.id)} /> <AddCircleOutline onClick={() => GAdd(country.id)} />
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                    <Grid item xs={4} align="center">
                                        <Grid container spacing={0}>
                                            <Grid item xs={8} align="center">
                                                <Avatar sx={{ bgcolor: grey[400], width: 80, height: 80, fontSize: 60 }}>{renderMedals(country.silver)}</Avatar>
                                            </Grid>
                                            <Grid item xs={8} align="center">
                                                <RemoveCircleOutlineIcon onClick={() => SRemove(country.id)} /> <AddCircleOutline onClick={() => SAdd(country.id)} />
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                    <Grid item xs={4} align="center">
                                        <Grid container spacing={0}>
                                            <Grid item xs={8} align="center">
                                                <Avatar sx={{ bgcolor: amber['A700'], width: 80, height: 80, fontSize: 60 }}>{renderMedals(country.bronze)}</Avatar>
                                            </Grid>
                                            <Grid item xs={8} align="center">
                                                <RemoveCircleOutlineIcon onClick={() => BRemove(country.id)} /> <AddCircleOutline onClick={() => BAdd(country.id)} />
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                </Grid>
                            </Typography>
                            <button onClick={() => Delete(country.id)}>Delete</button>
                        </CardContent>
                    </Card>
                </Container>
            </div>
        </Box>
    );
}

export default Country;
