import React, { useState } from 'react';

const AddCountry = ({ onAdd }) => {
  const [formData, setFormData] = useState({
    name: '',
    display: false,
  });

  const toggleForm = () => {
    setFormData((prevFormData) => ({
      ...prevFormData,
      display: !prevFormData.display,
      name: prevFormData.display ? '' : prevFormData.name,
    }));
  };

  const handleChange = (e) => setFormData({ ...formData, [e.target.name]: e.target.value });

  const addCountry = () => {
    onAdd(formData.name);
    toggleForm();
  };

  const { display, name } = formData;

  return (
    <div>
      {display ? (
        <form>
          <input
            type="text"
            id="name"
            name="name"
            value={name}
            onChange={handleChange}
            placeholder="country"
            autoFocus
            autoComplete="off"
          />
          <button onClick={addCountry} type="button" disabled={name.trim().length === 0}>
            Save
          </button>
        </form>
      ) : (
        <button onClick={toggleForm} className="Toggle-form">
          New Country
        </button>
      )}
    </div>
  );
};

export default AddCountry;
